


































import ut from '../../utils/index.js';
import login from '../../components/login.vue'
export default {
	data() {
		return {
			check:false,
			codemsg:"获取验证码",
			userinf:{
				"idCard": "",
				"name": "",//姓名
				"password": "",//密码
				"password1": "",//确认密码
				"phone": "",//手机号
				"roleId": 0,
				"username": "",//用户名
				"vCode": "",//验证码
				"idCard":''
			}
		}
	},
	components: {
		login
	},
	onLoad() {
	},
	methods: {
		go_mine_login(){
			wx.redirectTo({
				url: '../mine/login'
			})
		},
		cg_active(){
			console.log(1)
			this.check=!this.check;
		},
		req_register(){
			if(!this.userinf.username){
				ut.totast('请输入用户名');
				return;
			}
			if(!this.userinf.name){
				ut.totast('请输入姓名');
				return;
			}
			if(!this.userinf.idCard){
				ut.totast('请输入身份证号');
				return;
			}
			if(!ut.checkmobile(this.userinf.phone)){
				ut.totast('请输入正确的手机号');
				return;
			}
			if(!this.userinf.vCode){
				ut.totast('请输入验证码');
				return;
			}
			if(!this.userinf.password){
				ut.totast('请输入密码');
				return;
			}
			if(this.userinf.password.length<8||this.userinf.password.length>32){
				ut.totast('密码为8到32位');
				return;
			}
			if(this.userinf.password!=this.userinf.password1){
				ut.totast('两次密码输入不一致');
				return;
			}
			if(!this.check){
				ut.totast('请阅读用户协议');
				return;
			}
			ut.request({
				data: this.userinf,
				url: "user/register",
				c:true
			}).then(data=>{
				this.go_mine_login();
			})
		},
		req_sendCode(){
			if(this.codemsg!="获取验证码"&&this.codemsg!="重新获取")return;
			if(!this.userinf.phone){
				ut.totast('请输入正确的手机号');
				return;
			}
			let codenum=120;
			let timer=setInterval(()=>{
				codenum--;
				this.codemsg=codenum+'s后重新获取';
				if(codenum==0){
					clearInterval(timer);
					this.codemsg="重新获取";
				}
			},1000)
			ut.request({
				data: {
					phone:this.userinf.phone
				},
				url: "user/sendCode"
			}).then(data=>{
			}).catch(data=>{
				clearInterval(timer);
				this.codemsg="重新获取";
			})
		}
	}
}
